.casino-online__tabs-btn {
  width: calc(50% - 2.5px);
  font-family: var(--font-title);
  position: relative;
  display: inline-block;
  padding: 9px 0;
  font-size: 13px;
  line-height: 22px;
  color: #D2E4DD;
  border-radius: 8px;
  background: transparent;
  transition: background-color 0.3s ease, color 0.3s ease, box-shadow 0.3s ease;
}
.casino-online__tabs-btn::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: #0B764F;
  border-radius: 100px;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.casino-online__tabs-btn::before {
  content: "";
  position: absolute;
  bottom: 2px;
  left: 0;
  width: 100%;
  height: 2px;
  background: #E7FD52;
  border-radius: 100px;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.casino-online__tabs-btn:first-child {
  margin: 0 5px 0 0;
}
.casino-online__tabs-btn:hover {
  color: #E7FD52;
}
.casino-online__tabs-btn:hover::after, .casino-online__tabs-btn:hover::before {
  opacity: 1;
}
.casino-online__tabs-btn-one._active::after, .casino-online__tabs-btn-one._active::before, .casino-online__tabs-btn-two._active::after, .casino-online__tabs-btn-two._active::before {
  opacity: 1;
}
.casino-online__tabs-btn-one._active, .casino-online__tabs-btn-two._active {
  color: #E7FD52;
}
.casino-online__list-one._hidden {
